import React from "react";
import DashboardWrapper from "../Shared/DashboardWrapper";
import {
  AiOutlineMedicineBox,
  AiOutlineUser,
  AiOutlineHistory,
  AiOutlineSchedule,
  AiOutlineCalendar,
} from "react-icons/ai";
import { FaUserMd, FaUserFriends, FaBookMedical } from "react-icons/fa";
import ServicesCard from "../Home/ServiceCard";
import { useSelector } from "react-redux";

const Dashboard = () => {
  const user = useSelector((state) => state.authState.user);
  console.log({ user });
  const services = [
    {
      icon: <AiOutlineMedicineBox size={35} className="text-black" />,
      title: "Services",
      description: "Browse available services",
      link: "/services",
    },
    {
      icon: <FaUserMd size={35} className="text-black" />,
      title: "Doctors",
      description: "View available doctors",
      link: "/doctors",
    },
    {
      icon: <FaUserFriends size={35} className="text-black" />,
      title: "Patients",
      description: "Manage patient records",
      link: "/patients",
    },
    {
      icon: <AiOutlineHistory size={35} className="text-black" />,
      title: "History",
      description: "View patient history",
      link: "/history",
    },
    {
      icon: <AiOutlineHistory size={35} className="text-black" />,
      title: "ADD Patient",
      description: "Add a new patient",
      link: "/addpatient",
    },
    {
      icon: <AiOutlineHistory size={35} className="text-black" />,
      title: "ADD Doctor",
      description: "Add a new Doctor",
      link: "/adddoctor",
    },
    // {
    //   icon: <AiOutlineCalendar size={35} className="text-black" />,
    //   title: "Appointments",
    //   description: "Manage appointments",
    //   link: "/appointments",
    // },
    // {
    //   icon: <AiOutlineSchedule size={35} className="text-black" />,
    //   title: "Schedule",
    //   description: "View schedule",
    //   link: "/schedule",
    // },
    {
      icon: <FaBookMedical size={35} className="text-black" />,
      title: "Book Appointment",
      description: "Book an appointment",
      link: "/book-appointment",
    },
  ];

  return (
    <DashboardWrapper>
      <div className="min-h-screen flex flex-col lg:px-32 px-5 pt-0 lg:pt-0">
        <div className="flex flex-col items-center lg:flex-row justify-between">
          <div>
            <h1 className="text-4xl mt-1 font-semibold text-center lg:text-start">
              Welcome to Your Dental Clinic Dashboard
            </h1>
            <p className="mt-2 text-center lg:text-start text-gray-600">
              Manage your dental clinic efficiently and provide top-quality care
              to your patients with our comprehensive suite of services and
              tools.
            </p>
          </div>
        </div>
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-10 pt-14">
          {services.map((service, index) => (
            <ServicesCard
              key={index}
              icon={service.icon}
              title={service.title}
              description={service.description}
              link={service.link}
            />
          ))}
        </div>
      </div>
    </DashboardWrapper>
  );
};

export default Dashboard;
